const gifts = [brocha, candy, carrot, chicken, hat, game, glass, drum, money];

class Gift extends Component{
  constructor(props){
    super(props);
    this.state = {
      popGift: false,
    }
    this.handleGift = this.handleGift.bind(this);
  }

  handleGift() {
    const { onStore } = this.props;
    onStore(storeSections.OPEN_STORE);
    //const { popGift } = this.state;
    //this.setState({ popGift: !popGift });
  }

  render() {
    const { popGift } = this.state;
    const { className } = this.props;
    return (
      <div className="gift_cont">
        <button title="Obsequiar" onClick={this.handleGift} id="gift_button" className={`btn_social animated ${className}`}>
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
            <path d="M32 448c0 17.7 14.3 32 32 32h160V320H32v128zm256 32h160c17.7 0 32-14.3 32-32V320H288v160zm192-320h-42.1c6.2-12.1 10.1-25.5 10.1-40 0-48.5-39.5-88-88-88-41.6 0-68.5 21.3-103 68.3-34.5-47-61.4-68.3-103-68.3-48.5 0-88 39.5-88 88 0 14.5 3.8 27.9 10.1 40H32c-17.7 0-32 14.3-32 32v80c0 8.8 7.2 16 16 16h480c8.8 0 16-7.2 16-16v-80c0-17.7-14.3-32-32-32zm-326.1 0c-22.1 0-40-17.9-40-40s17.9-40 40-40c19.9 0 34.6 3.3 86.1 80h-86.1zm206.1 0h-86.1c51.4-76.5 65.7-80 86.1-80 22.1 0 40 17.9 40 40s-17.9 40-40 40z" />
          </svg>
        </button>
        <Popover isOpen={popGift} target="gift_button" placement="bottom">
          <PopoverBody>
            <ul className="gifts_cont">
              {
                gifts.map((gift) => <li key={uuid()}>{gift}</li> )
              }
            </ul>
          </PopoverBody>
        </Popover>
      </div>
    )
  }
};

const mapDispatchToProps = dispatch => bindActionCreators({ onStore }, dispatch);

export default connect(null, mapDispatchToProps)(Gift);

